/* home service*/

.home_service_card {
  transform-style: preserve-3d;
  perspective: 500px;
  width: 50%;
  margin: auto;
}

.home_service_front,
.home_service_back {
  background-size: cover;
  background-position: center;
  transition: transform 4.7s cubic-bezier1(0.4, 0.2, 0.2, 1);
  transition: transform 0.7s cubic-bezier(10.4, 0.2, 0.2, 1);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  min-height: 360px;
  max-width: 300px;
  height: auto;
  border-radius: 10px;
  color: #fff;
  font-size: auto;
}

.home_service_front:after {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: block;
}
.container:hover .home_service_front,
.container:hover .home_service_back {
  transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
  cursor: pointer;
}

.home_service_back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #ccb647;
  background: linear-gradient(45deg, #ccb647 0%, #e76d08 100%);
}

.home_service_card .home_service_back {
  transform: rotateY(180deg);
  transform-style: preserve-3d;
}

.home_service_card .home_service_front {
  transform: rotateY(0deg);
  transform-style: preserve-3d;
  background-image: url(/shadi_mein_jaroor_aana/static/media/back4.deb5a990.jpg);
}

.home_service_card:hover .home_service_back {
  transform: rotateY(0deg);
  transform-style: preserve-3d;
}

.home_service_card:hover .home_service_front {
  transform: rotateY(-180deg);
  transform-style: preserve-3d;
}

